@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@100&family=Open+Sans+Condensed:wght@300&family=Plaster&family=Playfair+Display:wght@700&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;padding:0;font-family:'Barlow Condensed', sans-serif;box-sizing:border-box}body div.App{display:flex;flex-direction:column}body div.App section{display:flex;flex-direction:column}body div.App section div.title{text-align:center}body div.App section div.title h1.title-h1{font-family:'Playfair Display', serif;font-size:3rem;margin-bottom:0;border-bottom:0.01rem solid #ae621f;display:inline-block}body div.App section div.title h2.title-h1{font-family:'Barlow Condensed', sans-serif}section.App-aboutme{background-color:#c6b29b;height:100vh;width:100%}section.App-contact{background-color:#e4eaea;height:90vh;width:100%}section.App-contact div.icons{display:flex;justify-content:space-around;height:100%;align-items:center}section.App-contact div.icons div.container{text-align:center}section.App-contact div.icons div.container span.fas{font-size:8rem;color:#141a13}section.App-contact div.icons div.container p.container-data{font-size:1.3rem;font-weight:900}section.App-contact div.icons div.container-icon a{text-decoration:none}section.App-contact div.icons div.container-icon a span.fab{display:block;font-size:4rem;color:#141a13;margin:2rem 0;transition:.1s}section.App-contact div.icons div.container-icon a span.fab:hover{color:#383508;transform:scale(1.2)}section.App-name{background-color:#e4eaea;height:100vh;width:100%}section.App-name div.data{position:absolute;top:50%;left:50%;margin-right:-50%;transform:translate(-50%, -50%);text-align:center}section.App-name div.data h1{font-family:'Playfair Display', serif;font-size:6em;border-bottom:0.1rem solid #383508;margin:0;padding:1rem}section.App-name div.data h2{font-family:'Barlow Condensed', sans-serif;font-size:2em;color:#141a13}nav.App-nav{position:fixed;width:100%;height:13vh;top:0;left:0;background-color:#c6b29b;z-index:1}nav.App-nav ul.App-nav-ul{margin:0;display:flex;list-style:none;justify-content:center;height:100%;align-items:center}nav.App-nav ul.App-nav-ul li{margin:1rem;font-family:'Barlow Condensed', sans-serif;font-size:1.6rem;text-transform:uppercase;cursor:pointer}nav.App-nav ul.App-nav-ul li:hover{border-bottom:0.01em solid #ae621f}section.App-technology{background-color:#c6b29b;height:100vh;width:100%;display:flex}section.App-technology div.technology-icons{display:flex;flex-wrap:wrap;justify-content:space-around;height:100%;align-items:center}section.App-technology div.technology-icons div.technology-icon{text-align:center}section.App-technology div.technology-icons div.technology-icon span.fab{font-size:7rem;transition:.5s}section.App-technology div.technology-icons div.technology-icon span.fab:hover{transform:scale(1.2);color:#383508}section.App-technology div.technology-icons div.technology-icon p{font-size:1.5rem;transition:.5s}section.App-technology div.technology-icons div.technology-icon p:hover{transform:scale(1.2)}section.App-projects{background-color:#e4eaea;height:100vh;width:100%}section.App-projects div.App-projects-examples{justify-content:space-around;display:flex;height:100%;align-items:center}section.App-projects div.App-projects-examples div.App-projects-examples-example{padding:1rem;text-align:center}section.App-projects div.App-projects-examples div.App-projects-examples-example h2.example-h2{text-transform:uppercase}section.App-projects div.App-projects-examples div.App-projects-examples-example img.example-img{width:20rem}section.App-projects div.App-projects-examples div.App-projects-examples-example a{text-decoration:none;font-size:1.8rem;color:#000;transition:1s;padding:0.01rem 0.3rem;border-right:.01rem solid black;border-left:.01rem solid black}section.App-projects div.App-projects-examples div.App-projects-examples-example a:hover{border-right:none;border-left:none;border-top:0.01rem solid #ae621f;border-bottom:0.01rem solid #ae621f}

